<template>
    <div class="menu-setting">
        <div v-if="$route.name == '配置菜单'">
            <Button class="create" type="primary" to="/manage/gonghao/menu/create">配置新菜单栏</Button>
            <div style="clear: both;"></div>
            <Button v-for="(item, index) in menuList" class="create" type="warning" @click="tap(item)">{{item.code}}</Button>
            <Modal v-model="dialogVisible" title="提示" :transfer="false">
                <p>您打算进行那种操作？</p>
                <div slot="footer">
                    <Button type="primary" size="large" @click="updateOne">修改</Button>
                    <Button type="error" size="large" @click="deleteOne">删除</Button>
                </div>
            </Modal>
        </div>
        <router-view />
    </div>
</template>

<script>
    import Axios from 'axios'
    export default {
        data() {
            return {
                menuList: [],
                data_info: {},
                dialogVisible: false,
            };
        },
        mounted() {
            this.showMenuList();
            // sessionStorage.setItem("secName", this.$route.name);
        },
        methods: {
            tap(item) {
                this.dialogVisible = true;
                this.data_info = item;
            },
            showMenuList() {
                this.$axios.get('/menu').then(res => {
                    this.menuList = res.data.data;
                });
            },
            updateOne() {
                this.dialogVisible = false;
                this.$router.push({
                    name: '修改菜单',
                    params: this.data_info
                })
            },
            deleteOne() {
                this.$axios.get('/menu/del', {params: {id: this.data_info._id}})
                .then(res => {
                    this.dialogVisible = false;
                    this.showMenuList();
                    this.$Message.info(res.data.success)
                });
            }
        }
    };
</script>

<style scoped>
    .create {
        margin-bottom: 30px;
        float: left;
    }
</style>
